@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --main-color: #17223B;
  --medium-navy: #263859;
  --light-navy: #6B778D;
  --red: #FF6768;
}
.App{
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  color:#17223B;
  color:var(--main-color);
  background-color: white;
  font-family: 'Montserrat', sans-serif;
}
  

/* banner section */
.banner{
    background-image: url(/static/media/Background.83e7667c.png);
    width: 100vw;
    height: 70vh;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}
.hero-text{
    text-align: center;
    position: absolute;
    top: 20%;
    left:10%;
    
}
.title{
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;
    border-right: 5px solid var(--medium-navy);
    padding: 20px;
    width: 50%;
}
.info{
    margin-top: 4%;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    padding: 20px;
    color: var(--main-color);
    width: 50%;
    text-align: left;
}

/* membership section */
.membership{
    background-color: var(--main-color);
    width: 100vw;
    height: auto;
    padding: 40px;

}
.member-btn{
    color: white;
    font-size: 25px;
    text-transform: uppercase;
    background-color: var(--red);
    font-weight: 400;
    padding: 15px;

}
.member-btn:hover{
 border: 1px solid var(--red);
 background-color: var(--main-color);
color: white;
}

  /* modal design */
.btn-register{
    padding: 17px;
    text-transform: uppercase;
    border-radius: 2px;
    border: none;
    font-weight:bold;
}
.facebook{
    background-color: #3b5998;
    color: white; 
}
.google{
    background-color: white;
    border: 1px solid gray;
}
.email{
    background-color: rgb(36, 153, 85);
    color: white;
}
.link{
    text-decoration: none;
    color: black;
}

.btn-sign{
    padding: 10px;
    background-color: white;
    border: 1px solid var(--medium-navy);
    border-radius: 3px;
    width: 90px;
    color: var(--medium-navy);
    text-transform: uppercase;
    font-weight: 600;

}

/* media query */
@media only screen and (max-width: 600px) {
    .hero-text{
        text-align: center;
        position: absolute;
        top: 2%;
        left: 0px; 
    }
    .title{
        font-size: 1.5rem;
        padding: 20px;
        text-align: center;
        border: none;
        width: 100%;
    }
    .info{
        margin-top: 2%;
        font-size: 1rem;
        border:none;
        padding: 0px;
        width: 100%;
        text-align: center;
    }

}
@media only screen and (max-width: 992px) {
    .hero-text{
        text-align: center;
        position: absolute;
        top: 2%;
        left: 0px;
    }
    .title{
        font-size: 1.5rem;
        padding: 20px;
        text-align: center;
        border: none;
        width: 100%;
    }
    .info{
        margin-top: 2%;
        font-size: 1rem;
        border:none;
        padding: 0px;
        width: 100%;
        text-align: center;
    }

}

.course-info{
    border-radius: 3px;
    margin: 10px;
    box-shadow: 8px 8px var(--main-color);
}
.img-div{
    position: relative;
}
