    width: 30%;
    background-color: var(--red);
    color: white;
    padding: 20px;   
    
}
.footer{
    background-color: var(--main-color);
    width: 100%;
    position: relative;
    bottom: 0px;
    padding-top: 20px;
    margin-top: 20px;

}
.footer-logo{
    width: 2%;
}
.links a{
  text-decoration: none;
  font-size: 30px;
  color: var(--red);
  
}
