}

.about-image{
    width:500px
}
.about-text{
    font-size: 20px;
    width: 80%;
    border-left: 3px solid var(--red);
    padding: 15px
}
.trainer-info{
    position: relative;
}
.trainer img{
     height: 300px;
     width: 400px;
}
.trainer-details{
    display: none;
}
.trainer-info:hover{
    cursor: pointer;
}
.trainer-info:hover img{
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
    -webkit-filter: blur(1px);
            filter: blur(1px);
    transition: 0.5s ease;
}
.trainer-info:hover .trainer-details{
   display: block;
   position: absolute;
   background-color:rgba(192, 83, 83, 0.5);
   padding: 30px;
   color: white;
   font-weight: bold;
   height: 200px;
   width: 80%;
   top: 20%;
   text-align: left;
   
}

